    .NewProfile_Header .NewProfile_Grid .NewProfile_Grid2 .Grid2_lower .AboutTabs1 .Aboutleft {
        width: 35%;
    }
    .NewProfile_Header .NewProfile_Grid .NewProfile_Grid2 .Grid2_lower .AboutTabs1 .AboutInfoDiv {
        width: 40%;
    }
    .NewProfile_Header .NewProfile_Grid .NewProfile_Grid2 .Grid2_lower .AboutTabs1 .Tab1Div {
        padding: 0;
    }
}

